.fRAMEChild {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-xl);
  background-color: var(--color-mediumslateblue);
  width: 89px;
  height: 40px;
  display: none;
}
.three {
  position: absolute;
  top: 4px;
  right: -13px;
  font-weight: 800;
  display: inline-block;
  width: 88px;
  height: 16px;
  z-index: 1;
}
.fRAME {
  position: relative;
  border-radius: var(--br-xl);
  background-color: var(--color-mediumslateblue);
  width: 100%;
  height: 40px;
  text-align: left;
  font-size: var(--font-size-xl);
  color: var(--color-white);
  font-family: var(--font-inter);
}
